<template><section><h1>checkbox 组件</h1>
<blockquote>
<p>基础用法</p>
</blockquote>
<demo-block>
                  <div class="source" slot="source"><template>
    <x-checkbox :xchange="xonChange" :checked="false">点击文字就是点击了 复选框</x-checkbox> {{checkboxType}} <br>
    <checkbox-group :checkboxgroupdata="checkboxgroupdata" :checkboxselected="['Apple','Apple', 'Orange', 'Apple', 'Pear']" :selectarray="(e) => xonChangeC(1, e)" :xchange="(e) => xonChangeC(1, e)">
    </checkbox-group>  <span>{{checkout001}}</span> <br>

    <checkbox-group :checkboxgroupdata="checkObj" :checkboxselected="[{ label: 'Pear', value: 'Pear' }]" :selectarray="(e) => xonChangeC(2, e)" :xchange="(e) => xonChangeC(2, e)">
    </checkbox-group> <span>{{checkout002}}</span> <br>
    <checkbox-group :checkboxgroupdata="checkObj" :checkboxselected="['Apple', 'Pear']" :disabled="true" :selectarray="(e) => xonChangeC(3, e)" :xchange="(e) => xonChangeC(3, e)">
    </checkbox-group> <span>{{checkout003}}</span> <br>
    <checkbox-group :checkboxgroupdata="checkObj" :checkboxselected="['Apple', 'Pear']" :selectarray="(e) => xonChangeC(4, e)" :xchange="(e) => xonChangeC(4, e)">
    </checkbox-group> <span>{{checkout004}}</span> <br>
</template>

</div>
                  <h3>通过 <code>input 中 type=&quot;checkbox&quot;</code> 方式实现</h3>

