    <span class="hljs-tag">&lt;/<span class="hljs-name">checkbox-group</span>&gt;</span> <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>{{checkout002}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span> <span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">checkbox-group</span> 
        <span class="hljs-attr">:checkboxgroupdata</span>=<span class="hljs-string">"checkObj"</span> 
        <span class="hljs-attr">:checkboxselected</span>=<span class="hljs-string">"['Apple', 'Pear']"</span> 
