            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'e'</span>, e)
            <span class="hljs-keyword">let</span> checkoutNumber = <span class="hljs-string">`checkout00<span class="hljs-subst">${type}</span>`</span>
