</div></demo-block>
<blockquote>
<h2>简单复选框 配置</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>:xchange</td>
<td style="text-align:left">复选框事件 事件</td>
</tr>
<tr>
<td>:checked</td>
<td style="text-align:left">是否选中</td>
</tr>
</tbody>
</table>
<blockquote>
<h2>复杂复选框 配置， 支持数组中传  字符串、对象</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>:xchange</td>
<td style="text-align:left">复选框事件 事件</td>
</tr>
<tr>
<td>:checkboxgroupdata</td>
<td style="text-align:left">给复选框数据</td>
</tr>
<tr>
<td>:checkboxselected</td>
<td style="text-align:left">默认选中项，必须和传递的数据 完全相同</td>
</tr>
<tr>
<td>:selectarray</td>
<td style="text-align:left">回显，初始化 数据放在它的身上</td>
</tr>
<tr>
<td>:xchange</td>
<td style="text-align:left">给变复选框事件</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import { XCheckbox, CheckboxGroup } from 'vo-ui'
export default {
    components: {
        XCheckbox, CheckboxGroup
    },
    data() {
        return {
            checkboxType: '',
            checkboxgroupdata: ['Apple', 'Pear', 'Orange'],
            checkObj: [
            { label: 'Apple', value: 'Apple' ,  disabled: true},
            { label: 'Pear', value: 'Pear' },
            { label: 'Orange', value: 'Orange' }
            ],
            checkout001: [],
            checkout002: [],
            checkout003: [],
            checkout004: [],
        };
    },
    methods: {
        xonChange(e) {
            this.checkboxType = e
        },
        // 初始化 数据也放在它的身上
        xonChangeC(type, e) {
            console.log('e', e)
            let checkoutNumber = `checkout00${type}`
            this[checkoutNumber] = e.codes
        }
    }
};
</script>


